<template>
    <div>
        <TopNav mdl="sms" select="1"></TopNav>
        <div class="wyu-tips wyu-tips-warning">
            <p>1).这里是短信余额变化日志</p>
        </div>
        <div class="wyu-main">
            <div class="wyu-block" v-loading="loading">
                <el-form :inline="true" :model="search" class="search-form" >
                    <el-form-item label="短信类型">
                        <el-select size="small" v-model="search.type" placeholder="选择短信类型">
                            <el-option label="全部类型" :value="0"></el-option>
                            <el-option v-for="item in typeMap" :label="item.value" :value="item.key"></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="开始时间">
                        <el-date-picker v-model="search.bgTime" type="datetime" placeholder="选择日期时间"></el-date-picker>
                    </el-form-item>

                    <el-form-item label="结束时间">
                        <el-date-picker v-model="search.endTime" type="datetime" placeholder="选择日期时间"></el-date-picker>
                    </el-form-item>

                    <el-form-item>
                        <el-button size="small" @click="seachDatas" type="primary" >搜索</el-button>
                    </el-form-item>
                </el-form>

                <el-table :data="logList" style="width: 100%">
                    <el-table-column width="100" prop="log_id" label="ID"></el-table-column>

                    <el-table-column label="变化条数" prop="num"></el-table-column>

                    <el-table-column label="当前剩余" prop="this_num"></el-table-column>

                    <el-table-column label="类型" prop="type_mean"></el-table-column>

                    <el-table-column label="时间" prop="add_time"></el-table-column>
                </el-table>
                <div style="text-align: right" class="block">
                    <el-pagination @current-change="setPage" :page-size="limit"
                                   layout="total, prev, pager, next" :total="count">
                    </el-pagination>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import * as api from '@/api.js';
    import TopNav from "@/components/TopNav";
    export default {
        name: "smsLog",
        components: {TopNav},
        data: function() {
            return {
                search:{
                    bgTime:'',
                    endTime:'',
                    cat:-999,
                },
                typeMap: [],
                page:1,
                limit:10,
                count:0,
                logList:[],
                smsType: 0,
                sms_num: 0,
                loading:false,
                datas: [

                ],
            };
        },
        created() {
            this.getLogList();
        },
        methods: {
            setPage(res){
                this.page = res;
                this.getLogList();
            },
            seachDatas(){
                this.page = 0;
                this.getLogList();
            },
            getLogList(){
                var that = this;
                that.loading = true;
                api.post("/shop/admin.marketing.sms/smsNumChangeLog",{
                    appToken: that.$store.getters.appToken,
                    type:that.search.type,
                    bgTime:that.search.bgTime,
                    endTime:that.search.endTime,
                    page:this.page,
                }).then(res=>{
                    that.logList = res.list;
                    that.count=res.count;
                    that.typeMap= res.typeMap;
                    that.sms_num= res.sms_num;
                    that.loading = false;
                }).catch((error)=>{
                    that.loading = false;
                });
            },

        }
    };
</script>
